<template lang="html">
  <div class="hd-rate" :style="{ width: widthSize, height: heightSize }">
    <div class="hd-rate-mask" :style="{ width: percent, backgroundSize: widthSize }"></div>
    <ul class="hd-rate-list">
      <li class="hd-rate-item" @click="onclick(1)"></li>
      <li class="hd-rate-item" @click="onclick(2)"></li>
      <li class="hd-rate-item" @click="onclick(3)"></li>
      <li class="hd-rate-item" @click="onclick(4)"></li>
      <li class="hd-rate-item" @click="onclick(5)"></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hd-rate',
  props: {
    unique: String,
    size: [Number, String],
    unit: {
      type: String,
      default: 'px'
    },
    readonly: Boolean,
    score: {
      type: [Number, String],
      default: 0
    }
  },
  data () {
    return {
      value: this.score
    }
  },
  watch: {
    score (newScore) {
      this.value = newScore
    }
  },
  computed: {
    heightSize () {
      return this.size + this.unit
    },
    widthSize () {
      return this.size * 5 + this.unit
    },
    percent () {
      return this.value * 20 + '%'
    }
  },
  methods: {
    onclick (index) {
      if (index !== this.value) {
        this.value = index
        this.$emit('click-rate', index, this.unique)
      }
    }
  },
  beforeMount () {
    if (this.readonly) {
      this.onclick = () => {}
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b rate {
    display: inline-block;
    width: 250px;
    height: 50px;
    position: relative;
    background: url(./images/star.png) left top no-repeat;
    background-size: 100%;

    @e mask {
      position: absolute;
      left: 0;
      top: 0;
      width: 0;
      height: 100%;
      z-index: 2;
      background: url(./images/star.png) left bottom no-repeat;
      background-size: 250px;
    }

    @e list {
      display: flex;
      height: 100%;
      position: relative;
      z-index: 3;
    }

    @e item {
      width: 20%;
    }
  }
}
</style>



