<template lang="html">
  <div class="demo-rate">
    <div class="demo-rate-row">
      <h3 class="demo-rate-title">商品质量</h3>
      <rate unique="goods" @click-rate="callClickRate"></rate>
      <p class="demo-rate-score" v-text="goods"></p>
    </div>
    <div class="demo-rate-row">
      <h3 class="demo-rate-title">描述相符</h3>
      <rate unique="desc" @click-rate="callClickRate"></rate>
      <p class="demo-rate-score" v-text="desc"></p>
    </div>
    <div class="demo-rate-row">
      <h3 class="demo-rate-title">服务质量</h3>
      <rate unique="service" @click-rate="callClickRate"></rate>
      <p class="demo-rate-score" v-text="service"></p>
    </div>
  </div>
</template>

<script>
import Rate from 'components/rate/rate.vue'

export default {
  name: 'demo-rate',
  components: {
    Rate
  },
  data () {
    return {
      goods: 0,
      desc: 0,
      service: 0
    }
  },
  methods: {
    callClickRate (score, unique) {
      this[unique] = score
    }
  }
}
</script>

<style lang="pcss">
@namespace demo {
  @b rate {
    @e row {
      display: flex;
      align-items: center;
      padding: 20px 30px 0;
    }

    @e title {
      margin-right: 20px;
    }

    @e score {
      margin-left: 20px;
      padding: 6px 20px;
      background-color: #eee;
      border-radius: 6px;
      color: #f33;
      position: relative;

      &:after {
        content: '';
        border: 6px solid transparent;
        border-right: 12px solid #eee;
        position: absolute;
        left: -16px;
        top: 50%;
        margin-top: -8px;
      }
    }
  }
}
</style>



